img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  font-style: normal;
}

pre {
  font-family: inherit;
  margin: 0;
  text-decoration: none;
  font-style: normal;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  overflow-x: hidden;
  margin-right: calc(-1 * (100vw - 100%));
  scrollbar-gutter: stable both-edges;
}
