// стилизация кнопки смены темы (иконки)
main {
  position: relative;
}
.theme {
  width: 24px;
  height: 24px;
  padding: 2px 2px;
  position: absolute;
  top: 0px;
  right: 0%;
  transform: translateY(-150%);

  background-color: transparent;
  border: 1px solid $color-accent;
  border-radius: 5px;
  cursor: pointer;
  @media screen and (min-width: $tablet) {
    width: 32px;
    height: 32px;
    padding: 5px 5px;

    // bottom: 48px;
    // left: calc(50vw - 370px);
  }
  @media screen and (min-width: $desktop) {
    // left: calc(50vw - 498px);
  }
}

.change-theme {
  // margin: auto;
  width: 18px;
  height: 18px;
  @media screen and (min-width: $tablet) {
    width: 20px;
    height: 20px;
  }
}

// стилизация фона и модалки фильма темной темы
body.theme-dark {
  background-color: #333333;
  .theme {
    border: 1px solid $color-main;
  }
  .modal {
    background-color: #3d3d3d;
    .button-action {
      border-color: $color-main;
      color: $font-color-main;
      &:hover {
        background-color: #3d3d3d;
        border-color: $color-accent;
        color: $color-accent;
      }
    }
  }
  .film__title {
    color: #e6e6e6;
  }
  .film-profile__value {
    color: #e6e6e6;
  }
  .film-description {
    color: #e6e6e6;
    &__text {
      color: #e6e6e6;
    }
  }
}

// стилизация карточек фильма
.films_list.theme-dark {
  .film_title {
    color: #e6e6e6;
  }
}

// стилизация футера
.footer.theme-dark {
  background-image: linear-gradient(
    180deg,
    rgba(57, 57, 57, 0) 0%,
    rgba(50, 50, 50, 0.5) 12%,
    rgba(50, 50, 50, 0.9) 25%,
    rgb(50, 50, 50) 37%,
    rgb(50, 50, 50) 100%
  );
  .footer-text {
    color: #e6e6e6;
  }
  .footer-heart {
    color: #e6e6e6;
  }
  .footer__link {
    color: #e6e6e6;
  }
}
